<template>
  <v-card class="rounded-lg" width="100%" flat>
    <v-sheet class="d-flex" color="secondary">
      <v-container class="my-container pa-0" fluid>
        <v-card-title> </v-card-title>
        <v-card-text class="px-0">
          <v-row class="my-0">
            <v-col cols="12" md="3" xl="2">
              <v-treeview
                v-model="tree"
                :items="items"
                :active="active"
                @update:active="getActiveValue"
                item-key="id"
                color="white"
                class="pe-6 text-body-1"
                shaped
                activatable
                return-object
                open-on-click
                transition
              >
              </v-treeview>
            </v-col>
            <v-col class="d-flex">
              <v-scroll-y-transition mode="out-in">
                <v-card
                  :key="selected.id"
                  color="transparent"
                  width="100%"
                  min-height="70vh"
                  class="px-1"
                  flat
                >
                  <DiscriminateAgainst v-show="selected.id === 0" />
                  <LaborStandardsAct v-show="selected.id === 1" />
                  <DispatchedWorker v-show="selected.id === 2" />
                  <PartTime v-show="selected.id === 3" />
                </v-card>
              </v-scroll-y-transition>
            </v-col>
          </v-row>
        </v-card-text>
      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import DiscriminateAgainst from "@/components/daily-compliance/recruiting-and-hiring/recruitment-guide/DiscriminateAgainst.vue";
import LaborStandardsAct from "@/components/daily-compliance/recruiting-and-hiring/recruitment-guide/LaborStandardsAct.vue";
import DispatchedWorker from "@/components/daily-compliance/recruiting-and-hiring/recruitment-guide/DispatchedWorker.vue";
import PartTime from "@/components/daily-compliance/recruiting-and-hiring/recruitment-guide/PartTime.vue";

import { createInvoice } from "@/hooks/api.js";

export default {
  name: "Authorization",
  components: {
    DiscriminateAgainst,
    LaborStandardsAct,
    DispatchedWorker,
    PartTime,
  },
  data: () => ({
    tree: [],
    active: [
      {
        id: 0,
      },
    ],
    selected: {
      id: 0,
    },
    items: [
      {
        id: 0,
        name: "一、招募不能歧視",
      },
      {
        id: 1,
        name: "二、通常適用勞基法",
      },
      {
        id: 2,
        name: "三、不能面試派遣人員",
      },
      {
        id: 3,
        name: "四、開短期職缺有限制",
      },
    ],
  }),
  methods: {
    close() {
      this.active = [0];
      this.$emit("input", false);
    },
    getActiveValue(value) {
      this.selected = value[0];
      console.log(value);
    },
    hideAlert() {
      window.setInterval(() => {
        this.alert = false;
      }, 3000);
    },
    submit() {
      if (this.$refs.form.validate()) {
        createInvoice({
          merchant_trade_no: this.merchant_trade_no,
        })
          .then(() => {
            this.alertType = "success";
            this.alertText = "開立成功";
          })
          .catch((err) => {
            this.alertType = "warning";
            const code = err.response.data.code;
            switch (code) {
              case "09002":
                this.alertText = "此訂單已開立過發票";
                break;
              case "09003":
                this.alertText = "訂單號碼不存在";
                break;
              default:
                this.alertText = "開立失敗";
            }
          });
        this.alert = true;
        this.$refs.form.reset();
        this.hideAlert();
      }
    },
  },
};
</script>

<style scoped>
.my-container {
  color: #eeeeee;
  background: rgba(255, 255, 255, 0.12);
  border-radius: 8px;
}
</style>
